.panel-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  &--border {
    margin-top: 1.2rem;

    > h2 {
      border-top: 1px solid $grey;
      display: flex;
      flex: 1 1 100%;
    }
  }

  &--home,
  &--home-second {
    padding-top: 1.2rem;

    .panel--full-width.panel {
      padding-top: 0;

      .lined-text {
        margin-bottom: 9.5rem;
      }
    }
  }

  &--home-second {
    padding-top: 0;
  }

  &--full-page {
    padding-top: 3.5rem;
  }

  &--spacer-bottom {
    margin-bottom: 9.37rem;
  }

  &.secondary-panels,
  &.tertiary-panels {
    margin-top: 6rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    @include media-above($break-large) {
      margin-top: 9.3rem;
    }
  }

  .panel {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 2.2rem;

    &--no-margin {
      margin-top: 0;
      padding-top: 0;
    }

    &--button {
        min-height: 30rem;

        @include media-above($break-large) {
          min-height: 37rem;
        }
    }

    .row-team & {
      flex-flow: row wrap;
    }

    &.map {
      padding-bottom: 2.2rem;

      @include media-above($break-large) {
        padding-bottom: 0;
      }
    }

    &--border {
        border-top: 1px solid $black;
        word-break: break-word;

        &:nth-of-type(2){
          border-top: 0;

          @include media-above($break-large) {
            border-top: 1px solid $black;
          }
        }
    }

    .row-team & {
      margin-bottom: 8rem;

      @include media-above($break-large) {
        margin-bottom: 0;
      }
    }

    .row-copy & {
      h3 {
        margin-bottom: 5rem;
      }
    }

    &--full-width {
      padding-right: 0;
      padding-left: 0;

      &:first-of-type {
        .panel--fw-inner {
          margin-right: 0;
          margin-left: 0;

          @include media-above($break-large) {
            margin-right: 0.6rem;
          }
        }
      }

      &:nth-of-type(2) {
        .panel--fw-inner {
          margin-left: 0;
          margin-right: 0;

          @include media-above($break-large) {
            margin-left: 0.6rem;
          }
        }
      }

      .panel--fw-inner {
        display: flex;
        flex-direction: column;

        .row-team & {
            height: 100%;
            justify-content: flex-end;

            .panel--fw-inner-top {
              @include media-above($break-large) {
                min-height: 380px;
              }
            }
        }

        &.border-top {
          border-top: 1px solid $grey;
        }

        > img {
          width: 50%;
          margin: 0 auto;

          @include media-above($break-large) {
            margin: 0;
          }

          .row-team & {
              margin-top: auto;
          }
        }
      }

      h3 {
        border-top: 1px solid $grey;
        padding-left: 0;
        padding-top: 0.25rem;

        @include media-above($break-large) {
          padding-left: 1.5rem;
        }
      }

      .lined-text {
        margin-top: 0;
        margin-bottom: 6.25rem;

        @include media-above($break-medium) {
          margin-bottom: 8.75rem;
          padding-left: 3rem;
        }

        @include media-above($break-medium) {
          padding-left: 7.5rem;
        }
      }
    }

    .panel-text {
      margin-top: 0;

      .row-full--nopadding-right & {
        padding-right: 3rem;
      }
    }
  }

  .divider-bubble {
    display: none;

    @include media-above($break-wide) {
      margin-right: 1rem;
      margin-left: 1rem;
      align-items: center;
      justify-content: center;
      display: flex;
      font-size: 3.25rem;
      background: $white;
      height: 130px;
      width: 130px;
      text-transform: uppercase;
      border-radius: 130px;
    }
  }

  // Hide/show panels
  .click-panels {
    display: flex;
    flex: 1 1 100%;
    flex-direction: column;

    p {
      font-size: 2rem;
      text-align: left;

      @include media-above($break-large) {
        font-size: 3rem;
      }
    }

    .primary-panels {
      & > .panel h3 {
        font-size: 1.2rem;
        text-align: left;

        @include media-above($break-large) {
          font-size: 5.4rem;
        }
      }
    }

    .primary-panels-display,
    .tertiary-panels {
      & > .panel {
        display: none;
        padding: $half-gutter-width;
        padding-top: 1.5rem;

        @include media-above($break-large) {
          padding-top: 3.1rem;
        }

        &.js-active {
          display: block;
        }
      }

      // Multi-step panels
      &.panels-multi-step {
        .panel {
          h4 {
            font-size: 2.2rem;
            font-family: $font-family-headings;

            @include media-above($break-large) {
              font-size: 5.3rem;
            }
          }
        }
      }
    }

    .tertiary-panels {
      & > .panel {
        background: $white;
        padding: 1rem 1rem 0;

        @include media-above($break-large) {
          padding: 3rem 3rem 0;
        }

        p {
          margin: 0;
          font-size: 1.5rem;

          @include media-above($break-large) {
            font-size: 3rem;
          }
        }

        .button-expand {
          margin-bottom: 0;
          margin-top: 3.5rem;
          color: $white;

          &:hover {
            color: $black;
          }

          @include media-above($break-large) {
            margin-top: 6.5rem;
          }
        }

         a {
           color: $grey;

           &:hover {
             color: $lightgrey;
           }
         }
      }
    }

    .primary-panels-display {
      .panel2 {
        padding-top: 0;
        padding-left: 0;
        padding-right: 0;
      }

      &.panels-multi-step {
        .panel {

          &.panel1 {
            background: $white;
            margin-top: 7.6rem;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 7rem;
            padding: 60px 40px 0;

            @include media-above($break-large) {
              padding: 140px 120px 0;
            }
          }
          &.panel2 {
            margin-bottom: 7rem;
          }
        }
      }
    }

    .secondary-panels {
      align-items: stretch;
      align-content: stretch;

      & > .panel {
         h3 {
           font-size: 1.2rem;
           text-align:left;
           padding-bottom: 1.5rem;

           @include media-above($break-large) {
             font-size: 3.2rem;
             padding-bottom: 4.1rem;
           }
         }
      }
    }
  }

  &.primary-panels,
  &.secondary-panels {
    & > .panel {
      background-color: $white;
      display: flex;
      width: 50%;
      padding: $half-gutter-width;
      border-bottom: 1px solid $black;
      text-align: center;

      @include media-above($break-large) {
        text-align: left;
      }

      & > a {
        margin-top: auto;
        margin-bottom: auto;
        text-decoration: none;
        max-width: 684px;
        margin-left: auto;
        width: 100%;
      }

      &.panel-2 a {
        max-width: 684px;
        margin-right: auto;
        margin-left: 0;
        width: 100%;
      }

      h3 {
        font-size: 1.5rem;
        @include media-above($break-large) {
          font-size: 5.4rem;
        }
      }

      &.js-active {
        border-top: 1px solid $white;
        border-bottom: 0;
        background: $black;
        color: $white;
        position: relative;

        &:after {
          top: 100%;
          z-index: 1;
          left: 50%;
          border: solid transparent;
          content: " ";
          height: 0;
          width: 0;
          position: absolute;
          pointer-events: none;
          border-color: rgba(0, 0, 0, 0);
          border-top-color: $black;
          border-width: 25px;
          margin-left: -25px;

          @include media-above($break-large) {
            border-width: 50px;
            margin-left: -50px;
          }
        }

        a {
          color: $white;
        }
      }
    }
  }

  &.secondary-panels {
    & > .panel {
      border-bottom: none;

      &.js-active {
        border-top: none;
      }
    }
  }
}
